.footer__link {
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  font-weight: 500;
  font-size: 14px;
  line-height: 1;
}

@media screen and (max-width: 1024px) {
  .footer__inner {
    padding-right: 70px;
  }
}
@media screen and (max-width: 768px) {
  .footer__inner {
    display: block;
    padding-right: 0;
    text-align: center;
  }
  .footer__link {
    display: inline-block;
    margin-bottom: 20px;
  }
  .footer__text:not(:last-child) {
    margin-bottom: 20px;
  }
}